*,
*::after,
*::before {
  box-sizing: border-box; }

html {
  font-size: 100%;
  height: 100%; }

body {
  background-color: #e7e6e3;
  color: #555;
  display: flex;
  flex-flow: column;
  font-size: 1rem;
  font-family: "aileron", "Helvetica Neue", Helvetica, Arial, sans-serif;
  min-height: 100%;
  position: relative;
  -webkit-font-smoothing: antialiased; }

.container {
  overflow: hidden; }

.header {
  background-color: #f7f7f7;
  display: flex;
  justify-content: space-between; }

.brand {
  background-color: black;
  line-height: 0;
  padding: 0.75rem 1.25rem; }

.p-nav {
  align-self: center;
  font-size: 1rem;
  padding-right: 1rem; }
  @media only screen and (min-width: 768px) {
    .p-nav {
      font-size: 1.25rem;
      padding-right: 1.5rem; } }

.p-nav-list {
  list-style: none;
  margin: 0;
  padding: 0; }

.p-nav-item {
  display: inline-block;
  vertical-align: middle; }
  .p-nav-item:not(:last-child) {
    margin-right: 0.75rem; }
  .p-nav-item a {
    border-bottom: 2px solid transparent;
    color: #0194f2;
    text-decoration: none;
    transition: border 0.2s linear, color 0.2s linear; }
    .p-nav-item a:focus, .p-nav-item a:hover {
      border-bottom: 4px solid #015c97;
      color: #015c97; }
    .p-nav-item a:active {
      border-bottom: 6px solid #004978;
      color: #004978; }
    .p-nav-item a.is-current {
      color: #161616;
      border: 0; }

.main-landing {
  display: flex;
  flex: 1;
  flex-direction: column; }

.main-content {
  text-align: left; }

.section {
  align-items: center;
  display: flex;
  flex: 1;
  justify-content: center; }

.case-1 {
  background-color: #e3e3e3; }

.case-2 {
  background-color: #cecece; }

.case-3 {
  background-color: #bababa; }

.case-4 {
  background-color: #a5a5a5; }

.case-5 {
  background-color: #919191; }

@media only screen and (min-width: 768px) {
  .footer {
    position: absolute;
    bottom: 0; } }

.colophon {
  font-size: 0.813rem;
  margin: 0.5rem 1rem; }
  .colophon a {
    border-bottom: 1px solid transparent;
    color: #555;
    text-decoration: none;
    transition: border-bottom-color 0.2s linear; }
    .colophon a:hover {
      border-bottom-color: currentColor; }
  @media only screen and (min-width: 768px) {
    .colophon {
      margin: 1rem 1rem 1rem 1.5rem; } }
  .colophon .divider {
    display: inline-block;
    margin: 0 4px; }
